import Breadcrumb from "@/app/components/Breadcrumb";
import connectDB from "@/lib/mongodb";
import Contact from "@/models/Contact";

export const metadata = {
  title: "Contact Us - Ajit Properties",
};

async function getContactData() {
  try {
    await connectDB();
    const contact = await Contact.findOne().lean();
    return contact
      ? { phone: contact.phone, email: contact.email, address: contact.address }
      : { phone: "", email: "", address: "" };
  } catch (error) {
    console.error("Error fetching contact data:", error);
    return { phone: "", email: "", address: "" };
  }
}

export default async function ContactPage() {
  const contactData = await getContactData();
  const breadcrumbItems = [
    { label: "Home", link: "/" },
    { label: "Contact Us" },
  ];

  const template = `
            <div className="container">
                <div className="contact-info-wrapper">
                    <div className="section-title text-center">
                        <h2>    
                            Contact Us for Furnished Home Tours
                        </h2>
                        <p className="mt-3">
                            Schedule a tour today and explore our fully furnished homes. Experience stylish, move-in-ready spaces designed for <br> comfort, convenience, and modern living. Let us help you find your perfect fit.
                        </p>
                    </div>
                    <div className="row">
                        <div className="col-xl-4 col-lg-6 col-md-6">
                            <div className="contact-info-box">
                                <div className="icon">
                                   <i className="fa-solid fa-location-dot"></i>
                                </div>
                                <div className="content">
                                    <h3>Location</h3>
                                    <p>${contactData.address}</p>
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-4 col-lg-6 col-md-6">
                            <div className="contact-info-box">
                                <div className="icon">
                                  <i className="fa-solid fa-phone-xmark"></i>
                                </div>
                                <div className="content">
                                    <h3>Phone</h3>
                                    <a href="tel:${contactData.phone}">+91 ${contactData.phone}</a>
                                </div>
                            </div>
                        </div>
                        <div className="col-xl-4 col-lg-6 col-md-6">
                            <div className="contact-info-box">
                                <div className="icon">
                                   <i className="fa-solid fa-envelopes"></i>
                                </div>
                                <div className="content">
                                    <h3>Email</h3>
                                    <a href="mailto:${contactData.email}">${contactData.email}</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>`;

  return (
    <>
      <Breadcrumb title="Contact Us" items={breadcrumbItems} />

      <section className="contact-section fix section-padding">
        <div className="container">
          <div dangerouslySetInnerHTML={{ __html: template }} />
        </div>
      </section>
      {/* <!-- Contact Section Start --> */}
      <section className="contact-section-3 fix section-padding pt-0">
        <div className="container">
          <div className="contact-wrapper-10">
            <div className="section-title text-center mb-0">
              <h5 className="wow fadeInUp">
                <i className="flaticon-home"></i> Keep In Touch{" "}
                <i className="flaticon-home"></i>
              </h5>
              <h2 className="wow fadeInUp" data-wow-delay=".3s">
                Get in Touch with Our Lovely Team
              </h2>
            </div>
            <form action="contact.php">
              <div className="row g-4">
                <div className="col-lg-6 wow fadeInUp" data-wow-delay=".2s">
                  <div className="form-clt">
                    <input
                      type="text"
                      name="name"
                      id="name"
                      placeholder="Full Name"
                    ></input>
                  </div>
                </div>
                <div className="col-lg-6 wow fadeInUp" data-wow-delay=".4s">
                  <div className="form-clt">
                    <input
                      type="text"
                      name="email"
                      id="email"
                      placeholder="Email address"
                    ></input>
                  </div>
                </div>
                <div className="col-lg-12 wow fadeInUp" data-wow-delay=".2s">
                  <div className="form-clt">
                    <div className="form">
                      <select className="single-select w-100">
                        <option>Real Estate</option>
                        <option>Property management</option>
                        <option>Market analysis</option>
                        <option>Home interior</option>
                      </select>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12 wow fadeInUp" data-wow-delay=".6s">
                  <div className="form-clt">
                    <textarea
                      name="message"
                      id="message"
                      placeholder="Message Here*"
                    ></textarea>
                  </div>
                </div>
                <div className="col-lg-12 wow fadeInUp" data-wow-delay=".2s">
                  <button className="theme-btn" type="submit">
                    Send Message
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </section>

      {/* <!-- Map Section Start --> */}
      <div className="googpemap-2">
        <iframe
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d6678.7619084840835!2d144.9618311901502!3d-37.81450084255415!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x6ad642b4758afc1d%3A0x3119cc820fdfc62e!2sEnvato!5e0!3m2!1sen!2sbd!4v1641984054261!5m2!1sen!2sbd"
          style={{ border: 0 }}
          allowFullScreen=""
          loading="lazy"
        ></iframe>
      </div>
    </>
  );
}
